*:not(svg) {
  font-family: arial, sans-serif;
  box-sizing: border-box;
}

.page {
  min-width: 320px;
}

.link {
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.button {
  display: inline-block;
  text-decoration: none;
  font-size: 14px;
  background-color: #f8f9fa;
  border: 1px solid #f8f9fa;
  border-radius: 4px;
  color: #3c4043;
  font-family: arial, sans-serif;
  margin: 11px 4px;
  padding: 0 16px;
  line-height: 32px;
  height: 36px;
  min-width: 54px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.button:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-color: #f8f9fa;
  border: 1px solid #dadce0;
  color: #202124;
}

.button--primary {
  background: #1a73e8;
  border: 1px solid transparent;
  color: white;
}

.button--primary:hover {
  background: #2b7de9;
  color: white;
  border: 1px solid transparent;
  box-shadow: 0 1px 2px 0 rgba(66, 133, 244, 0.3),
    0 1px 3px 1px rgba(66, 133, 244, 0.15);
}

.header {
  display: flex;
  font-size: 13px;
  align-items: center;
  height: 60px;
}

.page__header {
  justify-content: flex-end;
  padding: 6px 8px;
}

.header__sign-in-wrapper {
  display: flex;
}

.header__app-icon {
  margin-left: 15px;
  width: 24px;
  height: 24px;
  padding: 8px;
  cursor: pointer;
}

.header__app-icon:hover {
  background-color: rgba(60, 64, 67, 0.08);
  border-radius: 100%;
}

.header__link {
  padding-left: 15px;
  color: rgba(0, 0, 0, 0.87);
}

.header__button {
  margin: 0 8px;
  padding: 0 23px;
}

/* Main */

.main {
  text-align: center;
}

.main__wrapper {
  padding: 20px 20px 16px;
}

.main__search-icon {
  width: 20px;
  height: 20px;
  color: #9aa0a6;
  fill: currentColor;
}

.main__search-wrapper {
  display: flex;
  border: 1px solid #dfe1e5;
  box-shadow: none;
  border-radius: 24px;
  height: 46px;
  max-width: 584px;
  align-items: center;
  margin: auto;
  margin-top: 6px;
  padding-left: 14px;
  padding-right: 8px;
  margin-bottom: 18px;
}

.main__search-wrapper:focus-within,
.main__search-wrapper:hover {
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-color: rgba(223, 225, 229, 0);
}

.main__search {
  width: 100%;
  margin-left: 13px;
  border: none;
  outline: none;
}

.main section {
  font-size: 13px;
}

.main__image-wrapper {
  min-height: 92px;
  max-height: 290px;
  height: calc(100vh - 560px);
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.main__copyright-claim{
    padding: 10px;
    color: rgb(112, 112, 112);
}

/* Footer */

.footer__country {
  display: flex;
  padding: 15px 30px;
  border-bottom: 1px solid #dadce0;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.54);
  background-color: #f2f2f2;
}

.footer__menu-wrapper {
  font-size: 14px;
  display: flex;
  padding: 0 20px;
  background-color: #f2f2f2;
  justify-content: space-between;
}

.footer__menu {
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  justify-content: space-around;
}

.footer__link {
  padding: 15px;
}

.footer__link .link {
  color: rgba(0, 0, 0, 0.54);
}

.page__footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}

@media all and (max-width: 1200px) {
  .footer__menu-wrapper {
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
}

@media all and (max-width: 649px) {
  .main__image-wrapper > img {
    height: 82px;
  }

  .header {
    justify-content: space-between;
  }
}

@media all and (max-width: 480px) {
  .main__image-wrapper > img {
    height: 72px;
  }
}

@media all and (max-width: 483px) {
  .footer__menu:first-child {
      display: none;
  }
}

@media all and (max-width: 319px) {
  .page__footer {
    margin-top: 26px;
    position: relative;
  }
}

@media all and (max-height: 514px) {
  .page__footer {
    margin-top: 26px;
    position: relative;
  }
}